import util from "../../utils/util.js";
import api from "../../utils/api.js";
export default {
	data() {
		return {
			editTitle1: '编辑',
			editTitle2: '编辑',
			isFullAddress: false,
			isEmpty: true,
			from:'',                     // 来自什么页面
			addressList:[],              // 地址数据列表
			hasAddressNum1:'',  
			hasAddressNum2:'',
			findIndex:''
		};
	},
	onLoad(options) {
		if(options.from){
			this.$data.from = options.from;  // findPage找料   fetchPage取送
			if(this.$data.from == 'findPage' || this.$data.from == 'fetchPage'){
				this.$data.findIndex = options.findIndex
			}
		}
	},
	onShow() {
		this.$data.editTitle1 = '编辑';
		this.$data.editTitle2 = '编辑';
		this.getAddressListData();
	},
	methods: {
		// 设置默认地址
		setDefaultAddress(item,index){
			this.$data.addressList.forEach((o,i)=>{
				o.is_default = 0;
			})
			this.$data.addressList[index].is_default = 1;
			api.defaultAddress({
				method:'POST',
				data:{
					id:item.id
				}
			}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					util.successTips(res.msg);
				}else{
					util.successTips(res.msg);
				}
			}).catch((res)=>{
				util.successTips(res.msg || res.message);
			})
		},
		// 去新建页面
		goNewAddress(){
			uni.navigateTo({
				url:'../addressNew/addressNew?type=new'
			})
		},
		// 编辑显示
		editShow(e) {
			let index = e.currentTarget.dataset.index;
			if (index == 1) {
				this.$data.editTitle2 = '编辑';
				this.$data.editTitle1 == '编辑'?this.$data.editTitle1 = '完成':this.$data.editTitle1 = '编辑'
			} else {
				this.$data.editTitle1 = '编辑';
				this.$data.editTitle2 == '编辑'?this.$data.editTitle2 = '完成':this.$data.editTitle2 = '编辑'
			}
		},
		// 设为默认
		setDefault(e) {
			console.log(e)
			let id = e.currentTarget.dataset.id,
				index = e.currentTarget.dataset.index;
			api.setDefaultAddress({
				method: 'POST',
				data: {
					address_id: id
				}
			}).then((res) => {
				let addressList = this.data.addressList;
				addressList.forEach((ele) => {
					if (ele.is_default == 1) {
						ele.is_default = 0;
					}
				})
				addressList[index].is_default = 1;
				this.setData({
					addressList
				})

				uni.showToast({
					title: '已设为默认地址',
				})
			})
		},

		// 编辑
		edit(id) {
			uni.navigateTo({
				url: `../addressNew/addressNew?type=edit&id=${id}`,
			})
		},

		// 删除
		del(id,index) {
			uni.showModal({
				title: '提示',
				content: '确定要删除吗？',
				// confirmColor: '#C81A29',
				success: (res) => {
					if (res.confirm) {
						console.log('用户点击确定');
						// 确定删除
						api.deleteAddress({
							method: 'POST',
							data: {
								id
							}
						}).then((res) => {
							uni.showToast({
								title: '删除成功',
							})
							let addressId = uni.getStorageSync('defaultAddress').id;

							if (addressId) {
								if (id == addressId) {
									uni.setStorageSync('defaultAddress', false);
								}
							}

							// 获取列表数据
							this.$data.addressList = [];
							this.$data.isEmpty     = true;
							this.getAddressListData();
						})

					} else if (res.cancel) {
						console.log('用户点击取消')
					}
				}
			})
		},
		// 获取收货地址列表数据
		getAddressListData() {
			api.listAddress({}).then((res) => {
				if (res.code == 0 || res.code == 200) {
					let addressList = res.data;
					let isEmpty = addressList.length <= 0 || addressList == null || addressList == undefined ? true : false;
					
					let hasAddressNum1 = 0,
						hasAddressNum2 = 0;
					addressList.forEach((v, i) => {
						if (v.area_id == 0) {
							hasAddressNum2++
						} else {
							hasAddressNum1++
						}
					})
					this.$data.addressList    = addressList;
					this.$data.isEmpty        = isEmpty;
					this.$data.hasAddressNum1 = hasAddressNum1;
					this.$data.hasAddressNum2 = hasAddressNum2;
					
				} else if (res.code == 1) {
					this.$data.addressList = [];
					this.$data.isEmpty     = true;
				}
			})
		},

		// 点击选中地址返回
		goBlack(item) {
			
			// 来自找料任务
			if (this.$data.from == 'findPage') {
				let data = {'address':item,'findIndex':this.$data.findIndex};
				this.$eventHub.$emit('findPage', data);
			}
			// 来自取送任务
			if(this.$data.from == 'fetchPage'){
				let data = {'address':item,'findIndex':this.$data.findIndex};
				this.$eventHub.$emit('fetchPage', data);
			}
			if(this.$data.from == 'taskPayPage'){
				let data = {'address':item};
				this.$eventHub.$emit('taskPayPage', data);
			}
			// 返回上一页
			wx.navigateBack({
				delta: 1
			})
		},
		
	}
}
